<template>
	<div class="skills-selection-modal">
		<input
			class="mx-input search"
			placeholder="Search..."
			type="text"
			@input="debouceSearch"
		/>
		<div class="overflow-handler">
			<div class="skills-grid">
				<div
					class="skills-slot"
					v-for="(skill, idx) in filterByName(skillsList)"
					v-bind:key="idx"
					:class="[qualityToCSS(skill.Variant)]"
					@click="onSelection(skill)"
				>
					<img class="skill-icon" :src="skill['Icon']" alt="" />
					<BasicTile :classes="'anim-enabled'">
						<span class="name">
							{{ getDisplayName(skill) }}
						</span>
						<div class="white-space-pre-wrap">
							{{ skill.Desc }}
						</div>
						<div class="white-space-pre-wrap">
							<!-- {{ getSkillStats(skill) }} -->
							<ul
								class="bonuses-container"
								v-if="getSkillStats(skill).length > 0"
							>
								<li
									class="stats-wrap"
									v-for="(stat, idx) in getSkillStats(skill)"
									v-bind:key="idx"
								>
									<span class="stat white-space-pre-wrap">
										{{ stat.Stat }}
										<!-- {{
											Number(stat.Val)
												.toFixed(1)
												.toLocaleString()
										}} -->
										{{ stat.Val }}
										<br />
									</span>
								</li>
							</ul>
						</div>
					</BasicTile>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import {
		skillsData,
		IsEverythingLoadedPromise,
	} from "../../utils/dataImporter";
	import { qualityToCss, QualityPriority } from "../../utils/utils";
	import BasicTile from "../BasicTile";
	const skillNameProp = "Item Name";

	export default {
		name: "SkillsSelectionModal",
		props: ["skillsData", "onModalClose", "skillSlot"],
		components: {
			BasicTile,
		},
		data() {
			return {
				skillsList: [],
				skillStats: [],
				searchText: "",
			};
		},
		methods: {
			getSkillIcon(name) {
				return "icons/skills.png";
			},
			getSkillStats(skill) {
				const stats = this.skillStats.filter((stat) => {
					return (
						`${skill["Variant"]} ${skill["Item Name"]}` ===
							stat["Variant"] &&
						!stat.Stat.toLowerCase().includes("pvp") &&
						!stat.Stat.toLowerCase().includes("conflict")
					);
				});
				// const newStats = stats.map((stat) => {
				// 	// console.log("stat: ", stat);
				// 	for (const [key, val] of Object.entries(stat)) {
				// 		if (key === "Val") {
				// 			console.log(key, val);
				// 			// stat["Val"] = Number(val);
				// 		}
				// 	}
				// 	return stat;
				// });
				// console.log("newStats: ", newStats);
				return stats;
			},
			qualityToCSS(quality) {
				return qualityToCss[quality];
			},
			getDisplayName(skill) {
				// return skill["Variant"] === skill[skillNameProp]
				// 	? skill[skillNameProp]
				// 	: `${skill[skillNameProp]} (${skill["Variant"]})`;
				return `${skill[skillNameProp]} (${skill["Variant"]})`;
			},
			onSelection(skill) {
				this.$emit("close");
				this.onModalClose(skill);
			},
			debouceSearch(event) {
				clearTimeout(this.debounce);
				this.debounce = setTimeout(() => {
					this.searchText = event.target.value;
				}, 300);
			},
			filterByName(list) {
				return list.filter((skill) =>
					this.getDisplayName(skill)
						.toLocaleLowerCase()
						.includes(this.searchText.toLocaleLowerCase())
				);
			},
		},
		created() {
			Promise.all([
				IsEverythingLoadedPromise,
				skillsData.Skills,
				skillsData.SkillStats,
			]).then((data) => {
				// console.log("created: ", data);
				// this.BrandsData = data[1].reduce(function(o, val) {
				// 	o[val.Brand] = val;
				// 	return o;
				// }, {});
				// this.gearTalentsMap = data[3].reduce(function(o, val) {
				// 	o[val.Talent] = val;
				// 	return o;
				// }, {});
				// this.vendorGear = data[4].Gear[this.gearSlot];

				// this.skillsList = this.skillsData.sort(
				// 	(a, b) =>
				// 		QualityPriority[a["Quality"]] -
				// 			QualityPriority[[b["Quality"]]] ||
				// 		a[skillNameProp].localeCompare(b[skillNameProp])
				// );
				this.skillsList = data[1];
				this.skillStats = data[2];
			});
		},
		mounted() {},
	};
</script>

